.homepage {
  align-items: center;
  justify-content: space-around;
  min-height: 100vh;
  box-sizing: border-box;
}

.text {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: absolute;
  top: 30%;
  width: 90%;
}

.intro-text {
  width: 150px;
  font-size: 0.8em;
}

.big-name {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
}

.big-name h1 {
  font-family: "Playfair Display", serif;
  font-weight: bold;
  font-size: 10em;
  line-height: 1em;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  margin: 0;
  padding: 10px;
  text-align: right;
}

.bigName__Subtitle {
  margin-right: 50px;
  font-size: 1.1em;
}

.big-name p {
  position: relative;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 0.9em;
  line-height: 1em;
  display: flex;
  align-items: center;
  letter-spacing: 0.1em;
  justify-content: flex-end;
  color: #ffffff;
}

@media only screen and (max-width: 1150px) {
  .text {
    flex-direction: column;
    align-items: center;
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  .intro-text {
    text-align: center;
    width: 100%;
  }

  .big-name {
    align-items: center;
  }

  .big-name h1 {
    text-align: center;
  }
}

@media only screen and (max-width: 850px) {
  .big-name h1 {
    font-size: 18vw;
  }
  .bigName__Subtitle {
    font-size: 1.5vw;
  }
}
