.contact {
  margin-top: 100px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 40px;
}

.contact__title {
  font-size: 7em;
  font-family: "Playfair Display", serif;
  line-height: 1em;
}

.contact__details {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact__email {
  font-size: 1.1em;
}

.contact__linkedin {
  font-size: 1.1em;
}
