.footer {
  margin-top: 200px;
  font-size: 14px;
  position: relative;
  width: 100%;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer__row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: end;
  justify-items: center;
  position: relative;
  bottom: 0;
}

.footer__block {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.footer__top {
  display: flex;
  flex-direction: column;
}

.footer__top h2 {
  font-family: "Playfair Display", serif;
  padding: 0;
  margin: 0;
}

.download__resume {
  margin-right: 36px;
}

.socials {
  position: relative;
  right: 14px;
}

@media only screen and (max-width: 700px) {
  .footer__row {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    text-align: center;
  }
  .all_rights_reserved {
    grid-row: 6/7;
  }
  .download__resume {
    margin-right: 0px;
  }
  .socials {
    right: 0px;
  }
}
