.project__home {
  align-items: center;
  position: relative;
  width: 100%;
}

.row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.row > div {
  margin-bottom: 20px;
}

.row__netflix {
  grid-column: 2/7;
  display: flex;
  /* background: lightcoral; */
}

.row__netflix img {
  width: 100px;
  object-fit: contain;
  position: relative;
  /* left: 50px; */
}

.row__facebook {
  grid-column: 7/12;
  display: flex;
  /* background: lightgreen; */
}

.row__facebook img {
  width: 40px;
  position: relative;
}

.row__image {
  width: 35%;
  text-align: right;
  margin-right: 10px;
}

.row__right {
  background: white;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
}

.row__todo {
  grid-column: 1/6;
  display: flex;
  /* background: orange; */
}

.row__todo h2 {
  font-family: "Playfair Display", serif;
  position: relative;
  /* left: 30px; */
  padding-right: 12px;
}

.row__instagram {
  grid-column: 6/12;
  display: flex;
  /* background: red; */
}

.row__instagram img {
  position: relative;
  /* left: 50px; */
}

.projectHome__title {
  position: absolute;
  z-index: 10;
  font-family: "Playfair Display", serif;
  font-size: 1.5em;
}

ul {
  margin: 0;
  padding: 0;
}

.col__list > li {
  list-style: none;
  font-family: "Playfair Display", serif;
  font-weight: 400;
}

.row > div {
  transition: all 0.3s ease-in-out;
}

.row > div:hover {
  transform: scale(1.05);
}

.col__title {
  text-align: center;
}

.col__links {
  position: relative;
  bottom: -10px;
  display: flex;
  gap: 20px;
}

.col__links > a {
  color: #a0a2c9;
  text-decoration: none;
}

.col__buttons > button {
  margin-right: 10px;
}

@media only screen and (max-width: 1150px) {
  .row {
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (max-width: 750px) {
  .project__home {
    display: flex;
    flex-direction: column;
  }

  .projectHome__title {
    position: static;
    font-family: "Playfair Display", serif;
    font-size: 7vw;
    font-weight: 500;
    padding: 0 0 50px 0;
  }

  .row__netflix,
  .row__facebook,
  .row__instagram,
  .row__todo {
    align-items: center;
    flex-direction: column;
  }

  .row__image {
    width: auto;
    text-align: center;
    margin-bottom: 10px;
    margin-right: 0;
  }
}
