* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  background: #f7f1ef;
  padding-bottom: 0;
  margin-bottom: 0;
  color: #0f0f0f;
}

.container {
  width: 90%;
  margin: 0 auto;
}

html {
  scroll-behavior: smooth;
}

button {
  border: none;
  background: #9493b6;
  color: white;
  padding: 10px;
  border-radius: 30px;
  transition: all 0.1s ease-in;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  outline: none;
}

button:hover {
  color: #9493b6;
  background: white;
  outline: none;
  cursor: pointer;
}

a {
  color: black;
  transition: all 0.2s ease-in-out;
}

a:hover {
  color: #9493b6;
}
