.homepage_nav {
  position: relative;
  top: 30px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid white;
  width: 90%;
  margin: 0 auto;
  height: 50px;
}

.homepage_navLeft {
  display: flex;
  width: 10%;
  justify-content: space-between;
  margin-left: 10px;
  gap: 20px;
}

.homepage_navRight {
  margin-right: 10px;
}

.homepage__navItems {
  display: flex;
  gap: 20px;
}
