.project {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 50px;
}

.project__title {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 30px;
  padding: 50px;
}

.project .cols {
  display: flex;
}

.project .left-col {
  width: 60%;
}

.project .right-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 40%;
  margin-left: 50px;
  margin-top: 50px;
}

.project img {
  width: 100%;
}

.right-buttons {
  display: flex;
}
.right-buttons > * {
  margin-right: 20px;
}

.view__icon {
  margin-right: 10px;
  font-size: 24px;
}

.view-live,
.view-code {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 900px) {
  .project {
    text-align: center;
  }

  .project .cols {
    flex-direction: column;
    align-items: center;
  }
  .project__title {
    text-align: center;
    padding: 0;
    margin-bottom: 20px;
  }
  .project .left-col {
    width: 100%;
  }
  .project .right-col {
    width: auto;
    margin-left: 0px;
  }
}
